import { MenuFoldOutlined, SearchOutlined, QuestionCircleTwoTone, FilterOutlined } from "@ant-design/icons";
import { Button, Col, DatePicker, Drawer, Form, Row, Select, Tooltip, PageHeader, Popover, Tabs } from "antd";
import LocationPicker from "components/shared-components/LocationPicker";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import TableMenuItem from "components/shared-components/TableMenuItem";
import { COLORS } from "constants/ChartConstant";
import {
  DATE_FORMAT,
  LIGHT_NAVY_BLUE_COLOR,
  REPORTS_DESCRIPTION,
  SEARCH,
  SELECT_TEXT,
  SORT_DIRECTIONS,
  SELECT_METHODS,
} from "constants/CommonConstants";
import { MENU_ITEMS, MENU_ITEMS_TYPES } from "constants/EmployeeLoginDetails";
import { CHART_FORM_NAME, DATA_TABLE_COLUMNS, EXPORT_FILE_NAME, STATUSES } from "constants/UtilizationReportConstants";
import { FILTER_FORM_FIELDS } from "constants/CommonConstants";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import Chart from "react-apexcharts";
import { useDispatch, useSelector } from "react-redux";
import { showLoader } from "redux/actions/Loader";
import chartsService from "services/ChartsService";
import Utils from "utils";
import FileHandler from "utils/fileHandler";
import { CHILDREN } from "../../../../constants/CommonConstants";
import commonService from "services/CommonService";
import UtilizationReportSummary from "./UtilizationReportSummary";
import UtilizationReportVisual from "./UtilizationReportVisual";
import _ from "lodash";
import { setUtillizationReportColumns } from "../../../../redux/actions/MyTeams";

const visibileColumns = {
  employeeId: true,
  hrmsId: true,
  employeeName: true,
  employeeCountry: true,
  employeeLocation: true,
  parentAccountName: true,
  reservedForAccountName: true,
  projectName: true,
  allocationStatus: true,
  employeeDateOfJoining: true,
  // subDomainName: true,
  allocationStartDate: true,
  allocationEndDate: true,
  employeeTotalExperience: true,
  employeeDesignation: true,
  employeeEmail: true,
  functionalGroupName: true,
  // onBehalfOf: true,
  // domainName: true,
  // cclName: true,
  // subStatusAging: true,
  // subStatusStartDate: true,
  // subStatusEndDate: true,
  // subStatus: true,
  allocationAging: true,
  employeeStatus: true,
};
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
};
const { Option } = Select;
const ALL = "All";

const UtilizationReport = () => {
  useEffect(() => {
    getTechnicalFunctionalGroup();
    return () => {
      dispatch(showLoader(false));
    };
  }, []);
  const [functionalGroups, setFunctionalGroup] = useState([]);
  const getTechnicalFunctionalGroup = async () => {
    const technicalFunctionalGroup = await commonService.fetchAllFunctionalGroupsTechnical();
    if (technicalFunctionalGroup) {
      setFunctionalGroup(technicalFunctionalGroup);
    }
  };

  const dispatch = useDispatch();
  //const functionalGroups = useSelector(state => state?.myTeams?.masterData?.FunctionalGrp);

  const allLocations = useSelector(state => state.myTeams.allLocations);
  const accountsList = useSelector(store => store.myTeams.accountsList);
  const getMessage = useIntlMessage();
  const [openPopOver, setOpenPopOver] = useState(false);
  const [form] = Form.useForm();
  // NOTE: isFetching is a DUMB state all it has to do regenerate the columns once the data fetching is completed.
  const [isFetching, setIsFetching] = useState(false);
  const [employeeList, setEmployeeList] = useState([]);
  const [filteredEmployeeList, setFilteredEmployeeList] = useState([]);
  const [open, setOpen] = useState(false);

  const [isMenuVisible, setIsMenuVisible] = useState(false);
  const [activeTabKey, setActiveTabKey] = useState("report");
  const [series, setSeries] = useState([]);
  const [labels, setLabels] = useState([]);
  const [tableKey, setTableKey] = useState(0);
  const [reportParams, setReportParams] = useState();
  const [summaryParams, setSummaryParams] = useState();
  const [summaryData, setSummaryData] = useState([]);
  const [graphData, setGraphData] = useState([]);
  const [graphParams, setGraphParams] = useState();
  const [dynamicColumns, setDynamicColumns] = useState([]);
  const [initialVisibleColumns, setInitialVisibleColumns] = useState([]);
  const [visibility, setVisibility] = useState();
  const [quickActionBtns, setQuickActionBtns] = useState([]);
  const visibleFilteredColumns = useSelector(state => state.reports?.utillizationReportColumns);
  const [filteredVisibleData, setFilteredVisibleData] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [sortedInfo, setSortedInfo] = useState({});
  const options = {
    chart: {
      events: {
        dataPointSelection: (event, chartContext, config) => {
          filterTableDataByChart(config);
        },
      },
      toolbar: {
        show: true,
        export: {
          csv: {
            filename: EXPORT_FILE_NAME,
          },
          svg: {
            filename: EXPORT_FILE_NAME,
          },
          png: {
            filename: EXPORT_FILE_NAME,
          },
          jpeg: {
            filename: EXPORT_FILE_NAME,
          },
          pdf: {
            filename: EXPORT_FILE_NAME,
            orientation: "landscape",
            pageSize: "A4",
            margin: {
              top: 10,
              right: 10,
              bottom: 10,
              left: 10,
            },
          },
        },
      },
    },
    colors: COLORS,
    tooltip: {
      y: {
        formatter: value => {
          return `Count: ${value}`;
        },
      },
    },
    labels: labels,
    responsive: [
      {
        breakpoint: 480,
        options: {
          chart: {
            width: 200,
          },
          legend: {
            position: "bottom",
          },
        },
      },
    ],
  };
  const defaultParams = {
    onDate: moment().format(DATE_FORMAT),
  };
  const [searchParams, setSearchParams] = useState(defaultParams);
  let currentVisibleTableData = [];
  let searchInput = useRef(null);

  const handleShowLoader = useCallback(
    (isLoading = true) => {
      dispatch(showLoader(isLoading));
    },
    [dispatch],
  );

  useEffect(() => {
    if (
      searchParams &&
      searchParams.hasOwnProperty("accounts", "locations", "billabilityType", "onDate", "employeeStatus")
    ) {
      loadData(searchParams);
    }
  }, [activeTabKey]);

  const loadData = params => {
    switch (activeTabKey) {
      case "report":
        if (!_.isEqual(params, reportParams)) {
          fetchData(params);
          setReportParams(params);
        }
        break;
      case "summary":
        if (!_.isEqual(params, summaryParams)) {
          fetchSummary(params);
          setSummaryParams(params);
        }
        break;
      case "visual":
        if (!_.isEqual(params, graphParams)) {
          fetchGraphs(params);
          setGraphParams(params);
        }
        break;
    }
  };

  const fetchSummary = async params => {
    try {
      dispatch(showLoader(true));
      const response = await chartsService.getEmployeeByBillabillityTypeSummary(params);
      if (response) {
        setSummaryData(response);
      }
    } catch (e) {
      console.error(e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const fetchGraphs = async params => {
    try {
      dispatch(showLoader(true));
      const response = await chartsService.getEmployeeByBillabillityTypeGraphs(params);
      if (response) {
        setGraphData(response);
      }
    } catch (e) {
      console.error(e);
    } finally {
      dispatch(showLoader(false));
    }
  };
  const fetchData = async params => {
    handleShowLoader(true);
    setIsFetching(true);

    const promises = [chartsService.getEmployeeByBillabillityType(params)];
    const [employeeList] = await Promise.all(promises);
    const { data, quickActions, columns } = employeeList;
    if (!!columns.length) {
      if (!Utils.isEmpty(visibleFilteredColumns)) {
        setVisibility(visibleFilteredColumns);
        setInitialVisibleColumns(visibleFilteredColumns);
        setDynamicColumns(columns);
        setEmployeeBilliabilityData(data);
      } else {
        setEmployeeBilliabilityData(data);
        let initailColumns = Utils.createObjFromArray(columns);
        setVisibility(initailColumns);
        setInitialVisibleColumns(initailColumns);
        setDynamicColumns(columns);
      }
    }
    setQuickActionBtns(quickActions);
    handleShowLoader(false);
    setIsFetching(false);
  };

  const setEmployeeBilliabilityData = data => {
    setEmployeeList(data);
    setFilteredEmployeeList(data);
    const chartData = chartsService.preparedChartData(data);
    setLabels(chartData.labels);
    setSeries(chartData.series);
  };
  const getColumnSearchProps = field => {
    let filterObj = {
      filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />,
    };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => (
        <FilterOutlined
          id={`allocation-change-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      );
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          field={field}
          searchInput={searchInput}
          handleReset={handleReset}
          label={field.title}
          dataSource={filteredEmployeeList}
        />
      );
      filterObj.onFilter = (value, record) => {
        if (field.type === "date") {
          const _value = value.format(DATE_FORMAT).toString();
          const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
          return timeStampToDate.toLowerCase().includes(_value.toLowerCase());
        }
        return record[field.key]?.toString().toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    confirm();
  };

  const onSearch = values => {
    if (!Utils.isEmpty(values)) {
      const params = {
        billabilityTypes: values.reportType,
        onDate: values.selectDate,
        locations: values.city,
        accounts: values.account,
        //  employeeStatus: values.employeeStatus,
      };
      setSearchParams(params);
      loadData(params);
    }
  };

  const filterTableDataByChart = config => {
    const status = config.selectedDataPoints[0].length > 0 ? config.w.config.labels[config.dataPointIndex] : ALL;
    if (status === ALL) {
      setFilteredEmployeeList(employeeList);
    } else {
      const filteredData = employeeList.filter(item => item.allocationStatus === status);
      setFilteredEmployeeList(filteredData);
    }
  };

  const handleMenuVisibleChange = flag => {
    setIsMenuVisible(flag);
  };

  const onColHide = data => {
    const _visibility = { ...visibility, [data.key]: !visibility[data.key] };
    dispatch(setUtillizationReportColumns(_visibility));
    setVisibility({ ..._visibility });
  };

  const clearFilters = () => {
    setSearchParams(defaultParams);
    form.resetFields();
    // setTableKey(tableKey => tableKey + 1);
    setVisibility(visibileColumns);
    setIsMenuVisible(false);
  };

  const onMenuItemClick = ({ key }) => {
    switch (key) {
      case MENU_ITEMS_TYPES.clearAllFilters:
        clearFilters();
        break;
      case MENU_ITEMS_TYPES.exportAllExcel:
      case MENU_ITEMS_TYPES.exportVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllPDF:
      case MENU_ITEMS_TYPES.exportVisiblePDF:
        exportToPDF(key);
        break;
      case MENU_ITEMS_TYPES.exportAllVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllVisiblePDF:
        exportToPDF(key);
        break;
      default:
        return;
    }
  };

  const exportToExcel = type => {
    Utils.exportToExcel({
      type: type,
      dynamicColumns: dynamicColumns,
      reportData: employeeList,
      visibleReportData: currentVisibleTableData,
      visibility: visibility,
      fileName: EXPORT_FILE_NAME,
      currentDataSource: filteredVisibleData.length > 0 ? filteredVisibleData : employeeList,
    });
    setIsMenuVisible(false);
  };

  const exportToPDF = type => {
    Utils.exportToPDF({
      type: type,
      dynamicColumns: dynamicColumns,
      reportData: employeeList,
      visibleReportData: currentVisibleTableData,
      visibleReportColumns: filteredColumns,
      tableDynamicColumns: generateDynamicColumns(dynamicColumns),
      visibility: visibility,
      fileName: EXPORT_FILE_NAME,
      currentDataSource: filteredVisibleData.length > 0 ? filteredVisibleData : employeeList,
    });
    setIsMenuVisible(false);
  };
  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };
  const handleChange = (pagination, filters, sorter, extra) => {
    setFilteredVisibleData(extra.currentDataSource);
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };
  const generateDynamicColumns = columns =>
    columns.map(field => {
      return {
        title: (
          <Tooltip title={field.title} placement="top">
            <span>{field.title}</span>
          </Tooltip>
        ),
        ellipsis: "true",
        dataIndex: field.key,
        key: field.key,
        sorter:
          field.key === DATA_TABLE_COLUMNS.ALLOCATION_AGING.key
            ? null
            : (a, b) => Utils.antdTableSorter(a, b, field.key),
        sortDirections: SORT_DIRECTIONS,
        isVisible: visibility[field.key],
        ...getColumnSearchProps(field),
        render: (_, record) => {
          if (field.type === "date") {
            return record[field.key] ? moment(record[field.key]).format(DATE_FORMAT) : "";
          }
          if (field.type === "number") {
            return Utils.isEmpty(record[field.key]) ? record[field.key] : Number(record[field.key]);
          }
          return (
            <Tooltip title={record[field.key]} placement="top">
              <span id={field.key}>{record[field.key]}</span>
            </Tooltip>
          );
        },
      };
    });

  const filteredColumns = useMemo(
    () => generateDynamicColumns(dynamicColumns).filter(item => item.isVisible),
    [visibility, isFetching],
  );
  const handleOpenChange = newOpen => {
    setOpenPopOver(newOpen);
  };
  return (
    <>
      <Row align="middle" className="mb-3">
        <Col xs={24} sm={12} md={12} lg={24} xl={24}>
          <PageHeader
            className="p-2 pl-2"
            title={getMessage("sidenav.reports.utilization")}
            subTitle={
              <Popover
                placement="right"
                trigger="click"
                content={REPORTS_DESCRIPTION.utilizationReport}
                open={openPopOver}
                onOpenChange={handleOpenChange}
              >
                <QuestionCircleTwoTone className="font-size-md mb-0" />
              </Popover>
            }
          />
        </Col>
      </Row>
      {filteredEmployeeList.length === 0 ? null : (
        <Row justify="space-between" gutter={[20, 20]} className="mb-4">
          <Col lg={24} md={24}>
            <div className="ant-affix mt-2 menu-bar">
              <Button type="primary" onClick={showDrawer} shape="circle">
                <MenuFoldOutlined />
              </Button>
            </div>
          </Col>
        </Row>
      )}
      <Row align="middle" justify="space-between" gutter={[20, 20]} className="mb-2">
        <Col lg={12} md={24}>
          <Chart options={options} series={series} height={300} type="pie" />
        </Col>
        <Col lg={12} md={24}>
          <Form
            {...layout}
            form={form}
            name={CHART_FORM_NAME}
            onFinish={onSearch}
            initialValues={{
              selectDate: moment(),
            }}
          >
            <Form.Item
              label={getMessage(FILTER_FORM_FIELDS.SELECT_DATE.label)}
              name={FILTER_FORM_FIELDS.SELECT_DATE.name}
              rules={[{ required: true }]}
            >
              <DatePicker className={"w-100"} format={DATE_FORMAT} disabledDate={Utils.disableFutureDates} />
            </Form.Item>
            <Form.Item
              name={FILTER_FORM_FIELDS.REPORT_TYPE.name}
              label={getMessage(FILTER_FORM_FIELDS.REPORT_TYPE.label)}
              rules={[{ required: true }]}
            >
              <Select
                data-test-id="utilisation-report-type"
                showSearch
                optionFilterProp={CHILDREN}
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.REPORT_TYPE.label)}
                mode={SELECT_METHODS.MULTIPLE}
                onSelect={value => Utils.onMultiSelection(value, form, FILTER_FORM_FIELDS.REPORT_TYPE.name)}
              >
                <Option data-test-id="utilisation-report-type-all" value={ALL}>
                  {ALL}
                </Option>
                {functionalGroups &&
                  functionalGroups.map((item, index) => (
                    <Option key={index} value={item}>
                      Billability: {item}
                    </Option>
                  ))}
              </Select>
            </Form.Item>

            <Form.Item
              name={FILTER_FORM_FIELDS.CITY.name}
              label={getMessage(FILTER_FORM_FIELDS.CITY.label)}
              rules={[{ required: true }]}
            >
              <LocationPicker
                dataTestId="utilisation-report-location"
                locations={allLocations}
                name="city"
                form={form}
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.CITY.label)}
              />
            </Form.Item>
            <Form.Item
              name={FILTER_FORM_FIELDS.ACCOUNT.name}
              label={getMessage(FILTER_FORM_FIELDS.ACCOUNT.label)}
              rules={[{ required: true }]}
            >
              <Select
                data-test-id="utilisation-report-account"
                showSearch
                optionFilterProp={CHILDREN}
                mode="multiple"
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.ACCOUNT.label)}
                onSelect={value => Utils.onMultiSelection(value, form, FILTER_FORM_FIELDS.ACCOUNT.name)}
              >
                <Option data-test-id="utilisation-report-account-all" value={ALL}>
                  {ALL}
                </Option>
                {accountsList?.map((account, index) => (
                  <Option key={index} value={account.accountName}>
                    {account.accountName}
                  </Option>
                ))}
              </Select>
            </Form.Item>

            {/* <Form.Item
              name={FILTER_FORM_FIELDS.EMP_STATUS.name}
              label={getMessage(FILTER_FORM_FIELDS.EMP_STATUS.label)}
              rules={[{ required: true }]}
              initialValue={STATUSES.ALL_STATUS.value}
            >
              <Select placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.EMP_STATUS.label)}>
                {Object.values(STATUSES).map(e => {
                  return <Option value={e.value}>{getMessage(e.label)}</Option>;
                })}
              </Select>
            </Form.Item> */}

            <Form.Item {...tailLayout}>
              <Button
                type="primary"
                icon={<SearchOutlined className="mr-1" />}
                htmlType="submit"
                className="float-right"
              >
                {SEARCH.text}
              </Button>
            </Form.Item>
          </Form>
        </Col>
      </Row>

      <Tabs type="card" onChange={key => setActiveTabKey(key)}>
        <Tabs.TabPane className="pl-4 pr-3 pt-3" tab="Report" key="report" style={{ position: "relative" }}>
          {!!filteredEmployeeList.length && !!quickActionBtns.length && (
            <div className="is-mobile ">
              <div className="floating-btn floating-buttons ">
                {quickActionBtns.map(item => {
                  return (
                    <Tooltip title={item.description} placement="top">
                      <Button
                        type="primary"
                        className="floating-btn-circle my-1"
                        onClick={() => Utils.scrollIntoColumn(item.key)}
                      >
                        {item.title}
                      </Button>
                    </Tooltip>
                  );
                })}
              </div>
            </div>
          )}
          <TableComponent
            tableKey={tableKey}
            rowKey={(record, index) => `${record.employeeId}${index}`}
            columns={filteredColumns}
            data={filteredEmployeeList}
            scroll={{ x: true }}
            visibleData={data => {
              currentVisibleTableData = data;
            }}
            tableSize="small"
            visible={isMenuVisible}
            onDropdownMenuChange={handleMenuVisibleChange}
            showSorterTooltip={false}
            handleChange={handleChange}
          />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Summary" key="summary" className="p-3">
          <UtilizationReportSummary summaryData={summaryData} />
        </Tabs.TabPane>
        <Tabs.TabPane className="p-3" tab="Visual" key="visual">
          <UtilizationReportVisual graphData={graphData} />
        </Tabs.TabPane>
      </Tabs>
      <Drawer onClose={onClose} destroyOnClose visible={open} width={250}>
        <TableMenuItem
          columns={generateDynamicColumns(dynamicColumns)}
          onColHide={onColHide}
          menuItems={MENU_ITEMS}
          onMenuItemClick={onMenuItemClick}
        />
      </Drawer>
    </>
  );
};

export default UtilizationReport;
